<template>
  <b-container class="mt-4">
    <b-button
            class="topics-button"
            variant="link"
            v-for="topic in topics"
            :key="topic.id"
            @click="setTopic(topic.id)">
      {{ topic.title }}
    </b-button>

    <b-container class="mt-4" v-if="!threads.length">
      <h5>По данной теме пока не было обращений</h5>
    </b-container>

    <b-container class="text-left">
        <b-container class="mt-4 thread" v-for="thread in threads" :key="thread.id">
          <h5 class="thread-title">{{ thread.title }}</h5>
          <MessageComponent v-for="message in thread.messages"
                            :key="message.id"
                            :message="message">
          </MessageComponent>

        </b-container>
    </b-container>
    <div class="my-4">
      <button v-show="next"
              class="btn btn-sm btn-outline-success"
              @click="loadMore">Load more
      </button>
    </div>
  </b-container>
</template>

<script>

  import MessageComponent from "../components/MessageComponent";

  export default {
    name: "Home",
    data(){
      return {
        threads: [],
        messages: [],
        topics: [],
        currentTopic: null,
        next: null,
      }
    },
    components: {
      MessageComponent
    },
    methods: {
      async getThreads() {
        let endpoint = "http://127.0.0.1:8000/api/threads/";
        if (this.currentTopic) {
          endpoint = `http://127.0.0.1:8000/api/topic/${this.currentTopic}/`
        }
        const response = await fetch(endpoint);
        const { results, next } = await response.json();
        this.threads = results;
        if (next){
          this.next = next
        } else {
          this.next = null
        }
      },

      async loadMore() {
        let endpoint = this.next;
        const response = await fetch(endpoint);
        const { results, next } = await response.json();
        this.threads.push(...results);
        if (next){
          this.next = next
        } else {
          this.next = null
        }
      },

      async getTopics() {
        const response = await fetch("http://127.0.0.1:8000/api/topics/")
        this.topics = await response.json();
      },

      setTopic(topic_id){
        this.currentTopic = topic_id;
        this.getThreads()
      }
    },
    created() {
      this.getTopics()
      this.getThreads()
    }
  };
</script>

<style>
  .thread {
    margin-bottom: 40px;
  }
  .thread-title {
    text-decoration: underline;
    font-weight: bold;
    padding-left: 0;
  }
</style>