<template>
        <b-row class="mt-3" :style="indent_style">
                <b-col md="auto" class="photo-col">
                        <img class="photo"
                             :src="image_url.url" />
                </b-col>
                <b-col fluid>
                        {{ message.author_name }}:
                        <span v-html="message.text"></span>
                </b-col>
        </b-row>
</template>

<script>
        export default {
                name: "MessageComponent",
                props: {
                        message:{
                                type: Object,
                                required: true
                        },
                }
                ,
                computed: {
                        indent_style() {
                                return { 'margin-left': `${this.message.indent * 50}px` }
                        },
                        image_url() {
                                return{ url: 'http://127.0.0.1:8000' + this.message.author_photo }
                        }
                }
        }
</script>

<style scoped>
        .photo-col {
                width: 70px!important;
        }
        .photo {
                border-radius: 50%;
                width: 50px;
                height: 50px;
                border: 1px solid white;
        }
</style>